@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap');
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px #fff;
	-webkit-box-shadow: inset 0 0 6px #fff;
	border-radius: 10px;
	background-color: #fff;
}

::-webkit-scrollbar {
	width: 4px;
	background-color: #fff;
	border-radius: 5px;
	height: 6px;
}

::-webkit-scrollbar-thumb {
	border-radius: 10px;
	box-shadow: inset 0 0 6px #282f44;
	-webkit-box-shadow: inset 0 0 6px #282f44;
	background-color: #555;
}

body {
	background: linear-gradient(
		90deg,
		rgba(182, 182, 182, 1) 20%,
		rgba(33, 33, 33, 1) 20%
	);
}

/* -------------------- Fim Reset -------------------- */

.arrow {
	display: none;
	position: fixed;
	bottom: 20px;
	right: 20px;
	width: 40px;
	height: 40px;
	transform: translate(-50%, -50%);
	border-radius: 50%;
	background-color: #b6b6b6;
	border: 0;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	transition: all 0.1s ease-in-out;
	z-index: 999;
}

.arrow svg {
	width: 70%;
	height: 70%;
	fill: #171717;
}

.arrow:active {
	background-color: #555;
}

header {
	height: 60px;
	width: 100%;
	background-color: #2b2b2b;
	border-bottom: 1px solid #b6b6b6;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 20px;
	z-index: 999;
	position: fixed;
	top: 0;
	transition: all 0.3s ease-in-out;
}

header a {
	text-decoration: none;
}

header h1 {
	font-size: 1.5rem;
	color: #fff;
	font-weight: 700;
}

header nav {
	display: flex;
	align-items: center;
}

header nav ul {
	list-style: none;
}

header .nav-desktop ul {
	display: flex;
}

header .nav-mobile {
	display: none;
}

header .nav-desktop button,
header .nav-mobile button {
	color: #b6b6b6;
	text-decoration: none;
	font-size: 1.2rem;
	font-weight: 400;
	margin-left: 20px;
	transition: all 0.1s ease;
	display: block;
	background-color: transparent;
	border: 0;
	cursor: pointer;
}

header .nav button:hover,
header .nav-mobile button:active {
	color: #fff;
	scale: 1.05;
}

header .nav-mobile > button {
	font-size: unset;
	margin: 0;
	height: 60px;
	width: 60px;
	vertical-align: middle;
	outline: none;
	display: none;
}

header .nav-mobile button svg {
	width: 50%;
	height: 50%;
	vertical-align: middle;
}

header .nav-dropdown {
	z-index: 999;
	position: absolute;
	top: 60px;
	right: 0;
	width: 200px;
	overflow: hidden;
	transition: all 0.3s ease;
	border-radius: 0 0 2px 2px;
}

header .nav-dropdown ul {
	list-style: none;
	background-color: #2b2b2b;
}

header .nav-dropdown li {
	padding: 10px 0;
	border-left: 1px solid #b6b6b6;
	border-bottom: 1px solid #b6b6b6;
}

/*-------------------Display---------------------*/

section.display {
	margin-top: 60px;
	height: calc(100vh - 60px);
	width: 100%;
	display: flex;
	align-items: center;
	border-bottom: 2px solid #b6b6b6;
}

.cover-main {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 20px;
}

.cover-profile {
	position: relative;
	width: 300px;
	height: 300px;
}

.cover-profile img {
	width: inherit;
	height: inherit;
	border-radius: 50%;
}

.profile-overlay {
	width: inherit;
	height: inherit;
	background-color: #000;
	opacity: 0.5;
	position: absolute;
	top: 0;
	left: 0;
	border-radius: 50%;
	z-index: 1;
}

.cover-about {
	max-width: 450px;
	padding-left: 20px;
}

.cover-about h3 {
	font-size: 1.5rem;
	color: #fff;
	font-weight: 500;
	margin-bottom: 10px;
}

.cover-about h2 {
	font-size: 2.5rem;
	color: #fff;
	font-weight: 500;
}

.cover-about p {
	font-size: 0.8rem;
	color: #fff;
	font-weight: 400;
	margin-top: 10px;
	text-align: justify;
}

.cover-social {
	align-self: flex-end;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px;
}

.cover-social a {
	color: #b6b6b6;
	text-decoration: none;
	font-size: 2rem;
	font-weight: 400;
	transition: all 0.1s ease;
	margin: 0 10px;
}

.cover-social a:hover {
	color: #fff;
	scale: 1.2;
}

/*----------------------------Project---------------------*/

section.projects {
	width: 100%;
	background-color: #171717;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	padding: 20px 0;
	border-bottom: 2px solid #b6b6b6;
}

.projects h2 {
	font-size: 2rem;
	color: #fff;
	font-weight: 500;
	margin: 20px 0;
}

.projects-list {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	max-width: 1100px;
}

.project {
	flex-basis: 27%;
	height: 400px;
	background-color: #212121;
	padding: 10px;
	margin: 20px;
	border-radius: 5px;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
}

.project-image {
	border: 1px solid #333;
	width: 100%;
	min-height: 180px;
	max-height: 180px;
	background-color: #171717;
	background-size: cover;
	background-position: top;
	background-repeat: no-repeat;
	border-radius: 5px;
	margin-bottom: 10px;
}

.project-info {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin-bottom: 10px;
}

.project h3 {
	font-size: 1.1rem;
	color: #b6b6b6;
	font-weight: 500;
	margin-bottom: 10px;
}

.project span {
	font-size: 0.8rem;
	color: #828282;
	font-weight: 400;
}

.project p {
	flex-grow: 1;
	font-size: 0.7rem;
	color: #828282;
	font-weight: 400;
	margin-top: 10px;
}

.project-links {
	padding: 5px;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-items: center;
}

.project-links a {
	color: #b6b6b6;
	text-decoration: none;
	font-size: 1.2rem;
	font-weight: 400;
	transition: all 0.1s ease;
	margin: 0 10px;
}

.project-links svg {
	width: 30px;
	height: 30px;
	fill: #b6b6b6;
}

.project-links a:hover {
	color: #fff;
	scale: 1.2;
}

/*----------------------------Skills---------------------*/

section.skills {
	width: 100%;
	background-color: #212121;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	padding: 20px 0;
	border-bottom: 2px solid #b6b6b6;
}

.skills h2 {
	font-size: 2rem;
	color: #b6b6b6;
	font-weight: 500;
	margin: 20px 0;
}

.skills-list {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	align-items: center;
	max-width: 1000px;
	margin-bottom: 20px;
}

.skill {
	margin: 10px;
	flex-basis: 20%;
	flex-grow: 1;
	border-radius: 5px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}

.skills svg {
	width: 70px;
	height: 70px;
	fill: #b6b6b6;
}

.skill h4 {
	font-size: 1.1rem;
	color: #b6b6b6;
	font-weight: 500;
}

/*----------------------------Contact---------------------*/

section.contact {
	width: 100%;
	background-color: #171717;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	padding: 20px 0;
	border-bottom: 2px solid #b6b6b6;
}

.contact h2 {
	font-size: 2rem;
	color: #fff;
	font-weight: 500;
	margin: 20px 0;
}

.contact form {
	padding: 20px;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	max-width: 500px;
}

.contact form label {
	font-size: 1rem;
	color: #b6b6b6;
	font-weight: 500;
	margin: 10px 0;
	width: 100%;
}

.contact form input {
	width: 100%;
	height: 40px;
	background-color: #171717;
	border: 1px solid #333;
	border-radius: 5px;
	padding: 0 10px;
	margin: 10px 0;
	color: #b6b6b6;
	font-size: 0.8rem;
	font-weight: 400;
	outline: 0;
}

.contact form textarea {
	width: 100%;
	height: 150px;
	background-color: #171717;
	border: 1px solid #333;
	border-radius: 5px;
	padding: 10px;
	margin: 10px 0;
	color: #b6b6b6;
	font-size: 0.8rem;
	font-weight: 400;
	resize: none;
	outline: 0;
}

.contact form button {
	width: 100px;
	height: 40px;
	background-color: #212121;
	border: 1px solid #b6b6b6;
	border-radius: 5px;
	padding: 0 10px;
	margin: 10px 0;
	color: #b6b6b6;
	font-size: 0.8rem;
	font-weight: 400;
	cursor: pointer;
	transition: all 0.1s ease;
}

.contact form button:hover {
	background-color: #b6b6b6;
	color: #171717;
}

/*----------------------------Footer---------------------*/

footer {
	width: 100%;
	height: 70px;
	background-color: #171717;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	padding: 10px 0 0;
}

footer span {
	display: block;
	font-size: 0.8rem;
	color: #b6b6b6;
	font-weight: 400;
	padding-bottom: 10px;
}

/*----------------------------Media Queries---------------------*/

@media screen and (max-width: 1600px) {
	.cover-main {
		justify-content: flex-end;
	}
}

@media screen and (max-width: 1000px) {
	.cover-profile {
		width: 200px;
		height: 200px;
	}

	.cover-about h3 {
		font-size: 1.3rem;
	}

	.cover-about p {
		font-size: 0.7rem;
	}

	.skill {
		flex-basis: 30%;
	}
}

@media screen and (max-width: 900px) {
	body {
		background: #b6b6b6;
	}

	section.display {
		background: linear-gradient(
			180deg,
			rgba(182, 182, 182, 1) 20%,
			rgba(33, 33, 33, 1) 20%
		);
		height: unset;
		flex-direction: column;
		justify-content: center;
	}

	header .nav-desktop {
		display: none;
	}

	header .nav-mobile {
		display: block;
	}

	header .nav-mobile > button {
		display: block;
	}

	.cover-main {
		margin: 20px 0;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
	}

	.cover-about {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 20px;
	}

	.cover-about h3 {
		margin: unset;
	}

	.cover-social {
		width: 100%;
		flex-direction: row;
		padding-bottom: 20px;
	}

	.project {
		height: 450px;
	}
}

@media screen and (max-width: 768px) {
	.project {
		flex-basis: 50%;
		min-width: 350px;
	}

	.project h3 {
		font-size: 1.3rem;
	}

	.project span {
		font-size: 1rem;
	}

	.project p {
		font-size: 0.9rem;
	}

	.skills h2 {
		font-size: 1.7rem;
	}

	.skills svg {
		width: 50px;
		height: 50px;
		fill: #b6b6b6;
	}

	.contact h2 {
		font-size: 1.7rem;
	}
}
